.clearfix:after {visibility: hidden; display: block; font-size: 0; content: ''; clear: both; height: 0; }

html { box-sizing: border-box;}

*, *:before, *:after {box-sizing: inherit}

.srOnly { position: absolute; width: 1px; height: 1px; margin: -1px; border: 0; padding: 0; white-space: nowrap; -webkit-clip-path: inset(100%); clip-path: inset(100%); clip: rect(0 0 0 0); overflow: hidden;}

html {
  /* 16px * 62.5% = 10px */
  font-size: 62.5%; 
}

body {
  /* 10px * 1.6rem = 16px */
  font-size: 1.6rem; 
  font-family: 'Poiret One', cursive;
  font-family: 'Hind Siliguri', sans-serif;
  font-family: 'VT323', monospace;
  font-family: 'Press Start 2P', monospace;
  background: rgb(25, 53, 8); 
}

* {
  margin: 0;
  padding: 0;
}

img {
  /* height:auto will ensure the image's dimensions are consistent throughout its parent's varying width */
  width: 100%;
  height: auto;
  display: block;
  object-fit: cover;
}

a {
  text-decoration: none;
  color: inherit;
}

a:hover{
  outline: none;
}

ul, 
ol, 
li {
  list-style: none;
}

input, 
button {
  border: none;
  box-shadow: none;
  background: none;
}

button {
  cursor: pointer;
}

.wrapper {
  max-width: 500px;
  margin: 0 auto;
}

.App {
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}
@font-face {
  font-family: 'VT323';
  src: local('VT323'), url(/theNygmaMachine/static/media/VT323-Regular.bddd705e.ttf) format('truetype');
}

.landingPage {
  position: relative;
}

.preloader {
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  right: 0;
  z-index: 100;
  background: rgb(25, 53, 8);
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.preloader img {
  width: 20%;
  max-width: 150px;
  -webkit-animation: 800ms ease-out infinite loadermation;
          animation: 800ms ease-out infinite loadermation;
}

header {
  text-align: center;
  color: white;
}

main {
  /* I set an explicit height since the app will fit within the viewheight */
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
}

h1 {
  font-size: 4.5rem;
  text-transform: uppercase;
  margin: 0;
  letter-spacing: 5px;
}

h2 {
  font-family: monospace;
  margin: 20px;
  text-transform: lowercase;
}

.headImageContainer {
  width: 20%;
  margin: 30px auto;
  opacity: 1;
  min-width: 130px;
  max-width: 250px;
  -webkit-animation: 1s ease-in infinite flickerImage;
          animation: 1s ease-in infinite flickerImage;
}

form {
  width: 80%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.formInput {
  width: 90%;
  min-width: 250px;
  max-width: 600px;
  margin: 5px auto;
  padding: 2%;
  color: white;
  border: none;
  border-radius: 0;
  border-bottom: 5px solid white;
  font-family: monospace;
  font-size: 2rem;
  font-weight: 800;
  letter-spacing: 1.5px;
  text-align: center;
}

.formInput:focus {
    border-bottom: 5px solid #805FA0;
}

.formInput:hover {
  background: rgba(255, 255, 255, 0.5);
  color: rgb(25, 53, 8);
}

.formButton {
  margin: 35px auto;
  min-width: 225px;
  height:50px;
  color: white;
  border: 5px solid white;
  font-family: monospace;
  text-transform: uppercase;
  letter-spacing: 2px;
  font-weight: bold;
  font-family: 'Press Start 2P', monospace;
}

.formButton:focus, 
aside button:focus {
    border: 5px solid #805FA0;
    color: #805FA0;
    background: rgb(25, 53, 8);
}

.formButton:hover, 
aside button:hover{
  border: 5px solid rgba(25, 53, 8, 0.5);
  background: rgba(255, 255, 255, 0.5);
  color: rgb(25, 53, 8);
}


@-webkit-keyframes loadermation{
  0% {
    -webkit-transform: scale(0.75);
            transform: scale(0.75);
  }
  50% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  100% {
    -webkit-transform: scale(0.75);
            transform: scale(0.75);
  }
}


@keyframes loadermation{
  0% {
    -webkit-transform: scale(0.75);
            transform: scale(0.75);
  }
  50% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  100% {
    -webkit-transform: scale(0.75);
            transform: scale(0.75);
  }
}

@-webkit-keyframes flickerImage {
  0% {
    opacity: 1;
  }
  10% {
    opacity: 0.7;
  }
  50% {
    opacity: 0.9;
  }
  100% {
    opacity: 1;
  }
}

@keyframes flickerImage {
  0% {
    opacity: 1;
  }
  10% {
    opacity: 0.7;
  }
  50% {
    opacity: 0.9;
  }
  100% {
    opacity: 1;
  }
}

@media (max-width: 800px) {
  h1 {
    font-size: 3rem;
  }

  h2, 
  .formInput {
    font-size: 1.5rem;
    letter-spacing: 1px;
  }

  .formButton {
    font-size: 1.3rem;
  }

}


